@import url(https://fonts.googleapis.com/css?family=Oswald:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  display: block;

  width: 100%;
  min-height: 100vh;

  font-family: "Oswald", sans-serif;
  text-align: center;

  background: linear-gradient(45deg, #cc2b5e, #753a88);
}
.foodie-container {
  display: block;
  width: 100%;
  max-width: 600px;

  margin: 0 auto;
  padding: 2em 0 0;
}

h1 {
  display: block;
  width: 100%;

  padding: 0.5em 0;

  color: #fff;
  font-size: 2.4em;
  font-weight: 700;
}

#plcInput {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 1em auto;
  border: none;
  border-radius: 4px;

  font-size: 1em;
  font-weight: 700;
  text-align: center;

  padding: 0.5em 0;
}

button {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 0.5em auto;
  padding: 0.5em 0;

  border-radius: 4px;
  border: none;

  font-size: 1em;
  font-weight: 700;

  color: #fff;

  text-transform: uppercase;
}

.add-btn {
  background: #05dfd7;
}

.draw-btn {
  background: #52de97;
}

.restart-btn {
  background: #fe346e;
}

.plc-container {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 0 auto;
}

.plc-container p {
  display: block;
  width: 100%;

  color: #fff;
  font-size: 1.4em;
  padding: 0.5em 0;

  margin: 0.3em auto;

  background: rgba(0, 0, 0, 0.24);
  border-radius: 4px;

  cursor: pointer;
}

h2,
h3 {
  display: block;
  width: 100%;

  max-width: 350px;
  margin: 0 auto;

  color: #fff;
}

h2 {
  font-size: 2.4em;
  background: #4f3961;
  padding: 0.4em 0;
  border-radius: 4px;
}

h3 {
  font-size: 1.3em;
  padding: 0.8em 0;
}
