.foodie-container {
  display: block;
  width: 100%;
  max-width: 600px;

  margin: 0 auto;
  padding: 2em 0 0;
}

h1 {
  display: block;
  width: 100%;

  padding: 0.5em 0;

  color: #fff;
  font-size: 2.4em;
  font-weight: 700;
}

#plcInput {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 1em auto;
  border: none;
  border-radius: 4px;

  font-size: 1em;
  font-weight: 700;
  text-align: center;

  padding: 0.5em 0;
}

button {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 0.5em auto;
  padding: 0.5em 0;

  border-radius: 4px;
  border: none;

  font-size: 1em;
  font-weight: 700;

  color: #fff;

  text-transform: uppercase;
}

.add-btn {
  background: #05dfd7;
}

.draw-btn {
  background: #52de97;
}

.restart-btn {
  background: #fe346e;
}

.plc-container {
  display: block;
  width: 100%;
  max-width: 350px;

  margin: 0 auto;
}

.plc-container p {
  display: block;
  width: 100%;

  color: #fff;
  font-size: 1.4em;
  padding: 0.5em 0;

  margin: 0.3em auto;

  background: rgba(0, 0, 0, 0.24);
  border-radius: 4px;

  cursor: pointer;
}

h2,
h3 {
  display: block;
  width: 100%;

  max-width: 350px;
  margin: 0 auto;

  color: #fff;
}

h2 {
  font-size: 2.4em;
  background: #4f3961;
  padding: 0.4em 0;
  border-radius: 4px;
}

h3 {
  font-size: 1.3em;
  padding: 0.8em 0;
}